.popup__title {
	color: #000;
	font-style: normal;
	font-weight: 900;
	font-size: 24px;
	line-height: 29px;
	box-sizing: border-box;
	margin: 0;
	margin-bottom: 33px;
}

@media screen and (max-width: 370px) {
	.popup__title {
		font-size: 18px;
		line-height: 22px;
		margin-bottom: 75px;
	}
}
